.hero-container {
  position: relative;
  text-align: center;
  background: linear-gradient(to bottom right, #EED9EB, #81A5FE);
  height: 100vh; /* This will make the container take up the full height of the viewport */
}

.profile-image {
  width: 400px; /* Adjust the size as needed */
  height: auto; /* Maintain aspect ratio */
  position: absolute;
  left: 50%; /* Center the image horizontally */
  bottom: 0; /* Align the image to the bottom of the hero container */
  transform: translate(-50%, 0); /* Center the image horizontally */
  z-index: 1; /* Ensure the image is above other elements */
}

.title-container {
  position: absolute;
  top: 50%; /* Position the top of the container at the vertical center */
  left: 50%; /* Position the left of the container at the horizontal center */
  transform: translate(-50%, -50%); /* Center the container */
  z-index: 2; /* Ensure the text is above the image */
}

.main-title {
  font-size: 2rem; /* Large font size for the title */
  color: #2f2f63; /* Text color */
  width: 110%; /* Increased width */
  margin: 0 auto; /* Center the title block horizontally */
  text-align: center; /* Center the text within the title block */
}
